import imgAPI from '~/static/images/imgAPI'
import Title from '../Title'
import DotParallax from '../Parallax/Dot'
import CategoryCard from '../Cards/Category'

export default {
  components: {
    'title-main': Title,
    CategoryCard,
    DotParallax
  },
  data() {
    return {
      loaded: false,
      imgAPI: imgAPI,
      isRtl: false,
      categoriesData: [
        {
          img: imgAPI.education[0],
          title: 'Photography',
          desc: 'Nulla lobortis nunc vitae nisi semper semper.'
        },
        {
          img: imgAPI.education[4],
          title: 'Artificial Intelligence',
          desc: 'Nulla lobortis nunc vitae nisi semper semper.'
        },
        {
          img: imgAPI.education[7],
          title: 'Architect',
          desc: 'Nulla lobortis nunc vitae nisi semper semper.'
        },
        {
          img: imgAPI.education[2],
          title: 'Geography',
          desc: 'Nulla lobortis nunc vitae nisi semper semper.'
        },
        {
          img: imgAPI.education[6],
          title: 'Art',
          desc: 'Nulla lobortis nunc vitae nisi semper semper.'
        }
      ]
    }
  },
  mounted() {
    this.loaded = true
    setTimeout(() => {
      if (this.$vuetify.rtl) {
        this.isRtl = true
      }
    }, 200)
  },
  computed: {
    isDesktop() {
      return this.$mq === 'mdDown' || this.$mq === 'lgDown' || this.$mq === 'xl'
    },
    isMobile() {
      const xsDown = this.$store.state.breakpoints.xsDown
      return xsDown.indexOf(this.$mq) > -1
    }
  }
}
