const month = [
  'Jan',
  'Feb',
  'Mar',
  'Apr',
  'May',
  'Jun',
  'Jul',
  'Aug',
  'Sep',
  'Oct',
  'Nov',
  'Dec'
]

export default {
  data() {
    return {
      dateRaw: new Date(this.date)
    }
  },
  computed: {
    month() {
      return month[this.dateRaw.getMonth()]
    },
    day() {
      return this.dateRaw.getDate() < 10
        ? '0' + this.dateRaw.getDate()
        : this.dateRaw.getDate()
    },
    year() {
      return this.dateRaw.getFullYear()
    }
  },
  props: {
    category: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    date: {
      type: String,
      required: true
    }
  }
}
